.comunicacion {
  width: 100%;
  height: calc(100dvh - var(--topbar-height, 64px));
  max-height: calc(100dvh - var(--topbar-height, 64px));
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin: -24px;
  padding: 24px;
  box-sizing: border-box;
}

.comunicacionContent {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 0;
  gap: 0;
  overflow: hidden;
}

.topBar {
  position: sticky;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 16px;
  padding: 8px 0;
  background: var(--gray-50);
  border-bottom: 1px solid var(--gray-200);
  margin-bottom: 16px;
  flex-shrink: 0;
}

.header {
  display: flex;
  align-items: center;
}

.title {
  font-family: var(--font-display);
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--midnight);
  margin: 0;
}

.newBroadcastBtn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border: 1px solid var(--azure);
  background: var(--azure);
  color: var(--white);
  border-radius: var(--radius-md);
  font-family: var(--font-body);
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.newBroadcastBtn:hover {
  background: var(--azure-dark);
  border-color: var(--azure-dark);
}

.tabContent {
  flex: 1;
  overflow: hidden;
  min-height: 0;
  display: flex;
  flex-direction: column;
}

.mensajesTab {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  height: 100%;
  min-height: 0;
  overflow: hidden;
}

@media (max-width: 1024px) {
  .mensajesTab {
    grid-template-columns: 1fr;
  }
}

